import {
  Flex,
  Text,
  Spacer,
  Image,
  Stack,
  Input,
  InputGroup,
  InputRightElement,
  Circle,
  Box,
  HStack,
  Divider,
  SkeletonText,
  Spinner,
  useToast,
  Center,
} from "@chakra-ui/react";
import { lazy, Suspense, useState } from "react";
import Images_Icons from "../../constants/icons-images";
// import RedeemNavBar from "../../components/RedeemNavbar";
import { useJsApiLoader, GoogleMap, Marker } from "@react-google-maps/api";
import { useLocation, useNavigate } from "react-router-dom";
import { nanoid } from "@reduxjs/toolkit";
import copy from "copy-to-clipboard";
import Container from "../../components/Container";
import { GLOBAL_PREFIX } from "../../constants";

const RedeemNavbar = lazy(() => import("../../components/RedeemNavbar"));
// const HorizontalCarousel = lazy(
//   () => import("../../components/HorizontalCarousel")
// );
// const BrandsSliderCarousel = lazy(
//   () => import("../../components/BrandsSliderCarousel")
// );

//@dev - center function for working with Google Map configuration
const center = { lat: 48.8584, lng: 2.2945 };

export const Redeem = ({ customPadding }: any): JSX.Element => {
  let navigate = useNavigate();
  const toast = useToast();
  const [displaySwitcher, setDisplaySwitcher] = useState<boolean>(true);
  const { state }: any = useLocation();
  const [copyCouponText, setCopyCouponText] = useState<string>("BF2019");

  const {
    root,
    root2,
    nameStyles,
    boxCircleColor,
    leftSection,
    rightSection,
    smallButton,
    font1,
    font2,
    subHeadStyle,
  } = useRedeemStyles();

  const { isLoaded } = useJsApiLoader({
    //@ts-ignore
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
  });

  // if google map is not loaded. it will display the Skeleton Text
  if (!isLoaded) {
    return <SkeletonText />;
  }

  const handleCopyCouponText = (e: any) => {
    setCopyCouponText(e.target.value);
  };

  const copyCouponCodeToClipboard = () => {
    copy(copyCouponText);
    toast({
      position: "top-right",
      status: "info",
      duration: 5000,
      isClosable: true,
      render: () => (
        <Center color="white" p={3} bg="black" borderRadius={"10px"}>
          {`You have copied Coupon Code "${copyCouponText}"`}
        </Center>
      ),
    });
  };

  return (
    <Suspense fallback={<Spinner size="sm" />}>
      <Flex flexDir={"column"}>
        {/* ---------------------------------------------------------------------- REDEEM NAV BAR------------------------------------------------------------ */}
        <Suspense fallback={<Spinner size="sm" />}>
          <RedeemNavbar />
        </Suspense>
        {/* ---------------------------------------------------------------------- THE END REDEEM NAV BAR------------------------------------------------------------ */}
        <Container>
          <Stack {...root}>
            <Flex mb={"25px"}>
              {/* ---------------------------------------- CONGRAT You --------------------------------------------*/}
              <Text {...nameStyles}>Congrats you got this</Text>
              <Spacer />
              {/* ----------------------------------------ORDER SUMMARY --------------------------------------------*/}
              <Text {...nameStyles} textAlign={["right"]}>
                Order Summary
              </Text>
            </Flex>

            <Flex>
              {/* -----------------------------------LEFT SECTION---------------------------------------------- */}
              <Flex {...leftSection} flexDir={"column"}>
                {/* ----------------------------------------------------------------LEFT TOP DETAIL ----------------------------------------------------------------*/}
                <Flex {...root2}>
                  <Flex flexDir={"column"}>
                    <Text color={"black"} fontSize={"16px"} mb={"8px"}>
                      Access this deal later from your profile
                    </Text>
                    <Text color={"#999999"} fontSize={"14px"} mb={"10px"} maxWidth={"450px"}>
                      Designed for easy storage and comfortable adventure, the District Vision +
                      Suicoke Interlocking Sandals come equipped with
                    </Text>
                    <HStack>
                      <Box
                        as="button"
                        {...smallButton}
                        onClick={() => navigate(`${GLOBAL_PREFIX}/subsequentRedeeming`)}
                      >
                        <Text {...font2}>Got it</Text>
                      </Box>
                    </HStack>
                  </Flex>
                  <Spacer />
                </Flex>

                <Spacer />
                {/* ---------------------------------------------------------------- BUTTON SWITCHER ----------------------------------------------------------------*/}
                <Flex flexDir={"column"} mt={"30px"}>
                  <Text {...nameStyles}>How to redeem it</Text>
                  {/* ------------------------------ BUTTON switch between ONLINE AND OFFLINE ----------------------------- */}
                  <Flex flexDir={"row"} justify={"space-between"} mt={"40px"}>
                    <Box
                      as="button"
                      bg="#FAFAFA"
                      border={"1px solid rgba(0, 0, 0, 0.07)"}
                      color="black"
                      w={"50%"}
                      px={2}
                      borderBottom={displaySwitcher ? "2px solid #000000" : ""}
                      h={"50px"}
                      _focus={{
                        background: "#FFFFFF",
                        borderBottom: "2px solid #000000",
                      }}
                      onClick={() => setDisplaySwitcher(true)}
                    >
                      <Flex justifyContent={"center"} alignItems="center">
                        <Image src={Images_Icons.onlineIcon} mr="4px" />
                        Online
                      </Flex>
                    </Box>

                    <Box
                      as="button"
                      bg="#FAFAFA"
                      border={"1px solid rgba(0, 0, 0, 0.07)"}
                      color="black"
                      borderBottom={displaySwitcher ? "" : "2px solid #000000"}
                      px={2}
                      w={"50%"}
                      h={"50px"}
                      _focus={{
                        background: "#FFFFFF",
                        borderBottom: "2px solid #000000",
                      }}
                      onClick={() => setDisplaySwitcher(false)}
                    >
                      <Flex justifyContent={"center"} alignItems="center">
                        <Image src={Images_Icons.offlineIcon} mr="4px" />
                        Offline
                      </Flex>
                    </Box>
                  </Flex>
                </Flex>

                <Spacer />

                {/* ------------------------------ INFORMATION ABOUT ONLINE STORE ----------------------------- */}

                {displaySwitcher ? (
                  <Flex flexDir={"column"} mt={"40px"} mb={"40px"}>
                    <Flex flexDir={"column"} bg="#F2F2F2" borderRadius={"16px"} padding={"1.5rem"}>
                      <Text fontSize={"16px"} fontWeight={400} mb={"5px"}>
                        Apply this coupon code
                      </Text>
                      <Text fontSize={"14px"} color={"#999999"} maxWidth={"460px"} mb={"15px"}>
                        {" "}
                        for easy storage and comfortable adventure, the District Vision + Suicoke
                        Interlocking Sandals come equipped with. Apply this coupon code
                      </Text>
                      {/* ------------------------ Coupon code box copy --------------------- */}
                      <Flex>
                        <InputGroup size="md">
                          <Input
                            border={"1px solid rgba(0, 0, 0, 0.07)"}
                            borderRadius={"0px"}
                            outline="none"
                            bg="#FFFFFF"
                            type="text"
                            value={copyCouponText}
                            onChange={handleCopyCouponText}
                          />
                          <InputRightElement width="4.5rem">
                            <Box fontSize="12px" cursor={"pointer"}>
                              <Flex justifyContent={"space-between"} alignItems="center">
                                <Text>Copy code</Text>
                                <Image
                                  onClick={copyCouponCodeToClipboard}
                                  src={Images_Icons.copyIcon}
                                  mr="5px"
                                />
                              </Flex>
                            </Box>
                          </InputRightElement>
                        </InputGroup>
                      </Flex>

                      <Text fontSize={"14px"} color={"#999999"} maxWidth={"460px"} mt={"10px"}>
                        {" "}
                        for easy storage and comfortable adventure, the.
                      </Text>

                      <Flex>
                        <Text fontSize={"16px"} fontWeight={400} mt={"40px"} mb={"18px"}>
                          Redeem in the following sites
                        </Text>
                      </Flex>

                      <Divider orientation="horizontal" />
                      {/* ----------- Arc’teryx official store ---------- */}
                      {state?.brand?.online_stores?.map((e: any) => (
                        <Box key={nanoid()}>
                          <Flex flexDir={"row"} justify={"space-between"} mt={"30px"} mb={"18px"}>
                            <Text fontSize={"16px"}>{JSON.parse(e)?.name}</Text>
                            <Flex>
                              <Text
                                fontSize={"16px"}
                                fontWeight={"400px"}
                                textDecoration={"underline"}
                                as="a"
                                target="_blank"
                                href={`${JSON.parse(e)?.url}`}
                                mr={"7px"}
                              >
                                {JSON.parse(e)?.url}
                              </Text>
                              <Image src={Images_Icons.linkIcon} />
                            </Flex>
                          </Flex>

                          <Divider orientation="horizontal" />
                        </Box>
                      ))}
                    </Flex>
                  </Flex>
                ) : (
                  <Flex flexDir={"column"} mt={"40px"} mb={"40px"}>
                    <Flex flexDir={"column"} bg="#F2F2F2" borderRadius={"16px"}>
                      {/* ------------------------------- GOOGLE MAP IS  HERE ---------------------------- */}
                      <Box h="253px" w="100%" zIndex="modal">
                        {/* ------------------------------- GOOGLE MAP BOX ---------------------------- */}
                        <GoogleMap
                          center={center}
                          zoom={10}
                          mapContainerStyle={{
                            width: "100%",
                            height: "253px",
                            borderRadius: "10px 10px 0px 0px",
                          }}
                          options={{
                            zoomControl: false,
                            streetViewControl: false,
                            mapTypeControl: false,
                            fullscreenControl: false,
                          }}
                        >
                          {/* --------------------------- Directions and Markers are displaying inisde the Google Map ------------------------- */}
                          <Marker position={center} />
                        </GoogleMap>
                      </Box>

                      <Flex flexDir="column" padding={"1.5rem"} mt={"30px"}>
                        <Text fontSize={"16px"} fontWeight={400} mb={"5px"}>
                          Show this bar code
                        </Text>
                        <Text fontSize={"14px"} color={"#999999"} maxWidth={"460px"} mb={"8px"}>
                          {" "}
                          for easy storage and comfortable adventure, the District Vision + Suicoke
                          Interlocking Sandals come equipped with. Apply this coupon code
                        </Text>

                        {/* ------------------------------------------- BAR CODE ---------------------------------------- */}
                        <Box
                          bg="white"
                          w="100%"
                          h="100px"
                          display="flex"
                          justifyContent={"center"}
                          alignItems={"center"}
                        >
                          <Image src={Images_Icons.barCodeImage} />
                        </Box>

                        {/* -------------------------- REDEEM IN THE FOLLOWING SITES */}
                        <Flex>
                          <Text fontSize={"16px"} fontWeight={400} mt={"30px"} mb={"18px"}>
                            Redeem in the following sites
                          </Text>
                        </Flex>

                        <Divider orientation="horizontal" />
                        {/* ----------- Arc’teryx official store ---------- */}
                        {state?.brand?.physical_stores?.map((e: any) => (
                          <Box key={nanoid()}>
                            <Flex flexDir={"row"} justify={"space-between"} mt={"30px"} mb={"18px"}>
                              <Text fontSize={"16px"} fontWeight={"400px"}>
                                {JSON.parse(e)?.name}
                              </Text>
                              <Flex>
                                <Text
                                  fontSize={"16px"}
                                  textDecoration={"underline"}
                                  as="a"
                                  href="https://arcteryx.com/"
                                  mr={"7px"}
                                >
                                  https://arcteryx.com/
                                </Text>
                                <Image src={Images_Icons.linkIcon} />
                              </Flex>
                            </Flex>
                            <Divider orientation="horizontal" />
                          </Box>
                        ))}
                      </Flex>
                    </Flex>
                  </Flex>
                )}

                <Flex flexDir={"column"} mt="24px">
                  <Text {...nameStyles}>How it works</Text>
                  {[1, 2, 3]?.map((e: any) => (
                    <Flex key={nanoid()} mt="20px">
                      <Circle size="25px" {...boxCircleColor}>
                        <Text>{e}</Text>
                      </Circle>
                      <Text {...font1} ml={"10px"}>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Morbi dignissim
                        magna in nisi auctor, id aliquam ligula semper. Mauris congue finibus
                        tellus, sed eleifend elit cursus eu
                      </Text>
                    </Flex>
                  ))}
                </Flex>
              </Flex>

              <Spacer m={"1%"} />

              {/* -----------------------------------RIGHT SECTION---------------------------------------------- */}
              <Flex {...rightSection} flexDir={"column"}>
                <Flex>
                  <Image bg={"#00000018"} src={state?.images[0]} w={"100px"} h={"100px"} />

                  <Spacer mr={"6px"} />

                  <Flex flexDir={"column"}>
                    <Text fontSize={"13px"}>{state?.name}</Text>
                    <Spacer />
                    <Text fontSize={"14px"} color="#999999">
                      Black
                    </Text>
                    <Text fontSize={"14px"} color="#999999">
                      Size: {" S "}
                    </Text>
                    <Text fontSize={"14px"} color="#999999">
                      Quantity: {state?.quantity}
                    </Text>
                  </Flex>
                </Flex>
                {/* ------------------------------ Deal Price ------------------ */}
                <Flex
                  flexDir={"row"}
                  justify={"space-between"}
                  padding="6px"
                  mt={"20px"}
                  bg={"#F2F2F2"}
                >
                  <Text {...subHeadStyle}>Deal Price</Text>
                  <Flex>
                    <Box as="button" color="black" px={2} h={6}>
                      ${state?.price}
                    </Box>
                  </Flex>
                </Flex>

                {/* -------------------------------- Subtotal reward ---------------------------- */}
                <Flex flexDir={"row"} justify={"space-between"} padding="6px" bg={"#FFFFFF"}>
                  <Text {...subHeadStyle}>Subtotal rewards</Text>
                  <Flex>
                    <Box as="button" color="black" px={2} h={6}>
                      $0.00
                    </Box>
                  </Flex>
                </Flex>

                {/* -------------------------------- Difference ---------------------------- */}
                <Flex flexDir={"row"} justify={"space-between"} padding="6px" bg={"#F2F2F2"}>
                  <Text {...subHeadStyle}>Difference</Text>
                  <Flex>
                    <Box as="button" color="#C90303" px={2} h={6}>
                      ${state?.price}
                    </Box>
                  </Flex>
                </Flex>

                <Spacer />

                <Flex mt={"20px"}>
                  <Text fontSize={"14px"} color="#C90303">
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Morbi dignissim magna
                    in nisi auctor, id aliquam ligula semper.
                  </Text>
                </Flex>
              </Flex>
            </Flex>
          </Stack>
        </Container>
      </Flex>
    </Suspense>
  );
};

export default Redeem;

export const useRedeemStyles = () => {
  return {
    root: {
      w: "100%",
      // px: "10%",
      // px: [5, 5, 10, 20, 40],
      //   h: "458px",
      mt: "100px",
      mb: "20px",
      // pt: "3%",
      overflow: "hidden",
      // bg: "green",
    },
    root2: {
      bg: "#F2F2F2",
      h: "174px",
      w: "100%",
      borderRadius: "16px",
      p: "2%",
      overflow: "hidden",
    },
    nameStyles: {
      fontSize: ["20px", "20px"],
    },
    itemBox: {
      w: "100%",
      h: "3vw",
      bg: "white",
      border: "1px",
      justify: "space-between",
      align: "center",
      px: "5%",
    },
    boxCircleColor: {
      bgColor: "#000000",
      color: "white",
      fontWeight: "700",
    },
    smallButton: {
      w: "95px",
      h: "42px",
      bg: "#000000",
      borderRadius: "100px",
    },
    font1: {
      fontSize: "14px",
      lineHeight: "22px",
      color: "#999999",
    },
    font2: {
      fontSize: "18px",
      lineHeight: "22px",
      color: "#FFFFFF",
    },
    infoBox: {
      w: "60%",
      h: "10vw",
      px: "2%",
      borderBottom: "1px",
      borderBottomColor: "gray.300",
    },
    rootBox: {
      w: "100%",
      h: "100%",
      borderRadius: "15px",
      overflow: "hidden",
      borderWidth: "1px",
      borderColor: "#00000007",
    },
    leftSection: {
      h: "100%",
      w: "75%",
    },
    rightSection: {
      h: "100%",
      w: "25%",
      // p: "4px",
    },
    lowerSec: {
      w: "100%",
      align: "center",
    },
    headingStyle: {
      fontSize: ["14px", "12px", "14px", "16px", "18px"],
    },
    headingStyle2: {
      fontSize: ["14px", "12px", "14px", "16px", "18px"],
      color: "#999999",
    },
    subHeadStyle: {
      fontSize: ["14px", "12px", "12px", "12px", "14px"],
    },
    bodyStyle: {
      fontSize: ["14px", "14px", "16px", "16px", "18px"],
      color: "#000000",
    },
    button: {
      borderWidth: 1,
      borderColor: "#000000",
      px: "2%",
      py: "0.5%",
      borderRadius: "100px",
    },
    contain: {
      w: "100%",
      h: "64px",
      mt: "297px",
    },
    head: {
      fontSize: {
        base: "18px",
        md: "20px",
      },
    },

    smallerDesc: {
      fontSize: {
        base: "10px",
        md: "16px",
      },
    },

    container: {
      w: "100px",
      _hover: {
        background: "black",
        color: "white",
      },
    },

    search: {
      w: "240px",
      h: "40px",
      background: "#F6F6F6",
      borderRadius: "53px",
    },

    font: {
      fontSize: "16px",
      lineHeight: "19px",
      _hover: {
        color: "#FFFFFF",
      },
    },
    popfont: {
      fontSize: "12px",
      lineHeight: "14px",
      color: "#999999",
    },
  };
};
